.slider-wrapper--backend {
  overflow-x: hidden;
}

.slider-wrapper--backend .slider {
  position: relative;
  size: 100%;
  display: flex;
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  overflow-x: scroll;
  opacity: 1;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.slider-wrapper--backend .slider::-webkit-scrollbar {
  display: none;
}

.slider-wrapper--backend .slider__slide {
  scroll-snap-align: center;
  min-width: 100%;
  flex: 0 0 100%;
  padding: 4px;
  outline-offset: -2px;
}

.slider-wrapper--backend .flickity-page-dots {
  bottom: 4px;
}

.slider-wrapper--backend .flickity-page-dots,
.slider-wrapper--backend .flickity-prev-next-button {
  z-index: 1;
}

.slider-edit {
  position: fixed 0;
  z-index: 9998;
  background: #323232;
  padding: 10px 30px;
  color: #fff;
  overflow: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  opacity: 0;
  transition: opacity 0.2s ease;
  pointer-events: none;
}

.slider-edit__title {
  margin: 0.5em 0;
  padding: 0;
  font-size: 30px;
  text-align: left;
  color: inherit;
}

.slider-edit__content {
  position: relative;
  margin: 0 -15px;
  display: flex;
  flex-wrap: wrap;
}

.slide-edit {
  margin: 15px;
  flex: 0 0 330px;
}

.slide-edit__figure {
  position: relative;
  outline: 1px dashed #555;
  outline-offset: 6px;
  padding-top: 33.3333333333%;
  z-index: -1;
  height: 0;
  margin: 0;
}

.slide-edit__figure .neos-handle {
  padding: 10px;
}

.slide-edit__figure img {
  position: absolute;
  top: center;
  left: center;
  max-height: 100%;
}

.slider-page .slide,
.toggle-slider-empty {
  cursor: pointer;
}

.-slider--visible,
.-slider--visible body {
  overflow: hidden;
}

.-slider--visible body > *:not(.slider-edit):not(#neos-new-backend-container) {
  pointer-events: none;
}

.-slider--visible .slider-edit {
  opacity: 1;
  pointer-events: auto;
}
